import './App.css';
import axios from 'axios';
import { 
  useEffect, 
  useState 
} from 'react';
import Header from './components/header';
import Routes from './Routes';

//const public_url =   'http://localhost:8000/'
const public_url = 'https://sellobjects.herokuapp.com/'

export default function App() 
{ 
  const [is_auth, setAuth] = useState(false);
  const [result, setResult] = useState({});

  function signIn() {window.location.href=`${public_url}accounts/steam/login/`}

  function exit_acc() 
  {
    axios.get(`${public_url}accounts/logout/`,{withCredentials: true}).then(() => 
    { 
      setAuth(false); 
      setResult({}); 
      localStorage.clear();
    }).catch(() => 
    {
      setAuth(false);
      setResult({});
    })
  }

  useEffect(() =>{getInfo()},[])

    function getInfo() 
    { 
      axios.get(`${public_url}api/me/`, 
      {withCredentials: true}).then((skin) => 
      {
        setResult(skin.data);
        setAuth(true);
      }
      ).catch(() => {setAuth(false)})
   }

  return (
    <>
      <Header 
        signIn={signIn}
        exit_acc={exit_acc} 
        logged_in={is_auth} 
        account={result}
      />
      <Routes 
        default_link={public_url} 
        logged_in={is_auth} 
        account={result}
      />
    </>
  );
}